@import url(https://cdn.syncfusion.com/ej2/material.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.carousel .control-prev.control-arrow:before,
.carousel .control-next.control-arrow:before {
  border: 0 !important;
}

/* calender */

/* #calendar {
  width: 780px !important;
  max-width: 780px !important;
  min-width: 780px !important;
  height: 600px;
  padding: 10px;
} */
.e-day {
  font-size: 25px !important;
}

.e-calendar .e-content span.e-day {
  height: 61px !important;
  width: 61px !important;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
}

.e-calendar .e-content.e-month td,
.e-calendar .e-content.e-year td,
.e-calendar .e-content.e-decade td {
  padding: 25px 20px !important;
}

.e-week-header {
  display: none;
}

.e-calendar .e-content td.e-selected span.e-day {
  height: 61px !important;
  width: 61px !important;
}

.spec {
  border-radius: 50% !important;
  border: 7px solid #fc6273 !important;
  /* background-image: url("../public/Services\ &\ Activities\ lcons\ set1.svg") !important; */
}

.e-today > span,
.e-focused-date > span {
  background-color: #223771 !important;
  border: 0 !important;
  color: #fff !important;
  /* background-image: url("../public/Services\ &\ Activities\ lcons\ set1.svg") !important; */
}

.e-selected > span {
  background-color: #fd3232 !important;
  border: 0 !important;
  color: #fff !important;
}

.e-calendar .e-content .e-other-month span.e-day {
  display: none !important;
}

.e-title {
  width: 50% !important;
  justify-content: flex-start !important;
}

.e-calendar .e-header .e-date-icon-prev {
  -webkit-transform: rotate(-90deg) !important;
          transform: rotate(-90deg) !important;
  color: #223771 !important;
  padding-top: 7px !important;
}

.e-calendar .e-header .e-date-icon-next {
  -webkit-transform: rotate(-90deg) !important;
          transform: rotate(-90deg) !important;
  color: #223771 !important;
}

.e-calendar .e-header .e-icon-container {
  width: 227px !important;
  display: flex;
  justify-content: space-between;
}

/* .e-calendar .e-header.e-month .e-title {
  display: none;
} */

.e-calendar .e-header .e-icon-container .e-prev,
.e-calendar .e-header .e-icon-container .e-next {
  border-radius: 50%;
  border: 2px solid #223771;
}

.e-footer-container {
  display: none !important;
}

/* .e-calendar .e-content span.e-day div{
  position:absolute !important
} */

/*pagination*/
.ant-pagination-item-active a {
  color: #0c2e6e !important;
  text-decoration: underline !important;
}

/* .ant-pagination-next .ant-pagination-disabled button {
  background-color: #0041ba1a !important;
}

.ant-pagination-next .ant-pagination-disabled button span{
  color: #0c2e6e !important;
} */

/* time picker disable */
.ant-picker-time-panel {
  display: none !important;
}

/* Search bar */
.ant-popover-message > span {
  display: none !important;
}

.ant-popover-buttons {
  display: none !important;
}
.ant-popover-message-title {
  padding-left: 0 !important;
}
.ant-popover-message {
  padding: 0 !important;
}
.ant-input-search-button {
  background-color: #42a838 !important;
}

/* Scrollbar Styling */
/* ::-webkit-scrollbar {
  height: 15px;
  width: 8px;
}
::-webkit-scrollbar-track {
  background-color: #e4e4e4;
  -webkit-border-radius: 20px;
  border-radius: 20px;
}
::-webkit-scrollbar-thumb {
  -webkit-border-radius: 10px;
  border-radius: 20px;
  background: rgba(53, 51, 51, 0.3);
} */

@media only screen and (max-width: 425px) {
  .e-day {
    font-size: 20px !important;
  }

  .e-calendar .e-content span.e-day {
    height: 41px !important;
    width: 41px !important;
    display: flex !important;
    justify-content: center !important;
    align-items: center !important;
  }
  .e-calendar .e-content.e-month td,
  .e-calendar .e-content.e-year td,
  .e-calendar .e-content.e-decade td {
    padding: 12px 0px !important;
  }
}

